.my-library-header {
  //display: none;
  padding-top: 40px;
  padding-bottom: 60px;
  height: 230px;
  //background-color: rgba(0, 0, 0, 0.56);
  background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url('../images/hero-my-library/cinema-hall-background-mobile.jpg');
  @include retina {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url('../images/hero-my-library/cinema-hall-background-mobile-2x.jpg');
  }

  @include mobile {
    height: 216px;
  }

  @include tablet {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url('../images/hero-my-library/cinema-hall-background-tablet.jpg');
    @include retina {
      background-image: linear-gradient(
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('../images/hero-my-library/cinema-hall-background-tablet-2x.jpg');
    }
  }
  @include desktop {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url('../images/hero-my-library/cinema-hall-background-desktop.jpg');
    @include retina {
      background-image: linear-gradient(
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('../images/hero-my-library/cinema-hall-background-desktop-2x.jpg');
    }
  }
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
}

.my-library-header__container {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
  width: auto;
}

.my-library-header__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  fill: currentColor;
}

.my-library-header__logo {
  display: flex;
  align-items: center;
  transition-property: filter, color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  color: #ffffff;
  &:hover,
  &:focus {
    filter: drop-shadow(0px 4px 4px #ff6b02);
    color: #ff6b02;
  }
}

.my-library-header__name {
  display: none;
  font-style: normal;
  font-weight: 500;
  font-size: 30px;
  line-height: 35px;
  align-items: center;
  text-align: center;
  margin-left: 10px;
  color: currentColor;
  @include mobile {
    display: flex;
  }
}

.my-library-header__links__list {
  display: flex;
  margin-left: auto;
}

.my-library-header__links__item {
  margin-right: 40px;
  &:last-child {
    margin-right: 0;
  }
  &.--current {
    border-bottom: 3px solid #ff001b;
  }
}

.my-library-header__links__link {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  &:hover,
  &:focus {
    filter: drop-shadow(0px 4px 4px #ff6b02);
    color: #ff6b02;
  }
}

.my-library-header__buttons-list {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  @include tablet {
    gap: 32px;
  }
  @include desktop {
    gap: 16px;
  }
}

.my-library-header__button {
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;
  width: 130px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;

  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;

  min-width: 130px;

  &.--current {
    border: 1px solid #ff6b02;
    background-color: #ff6b02;
  }

  @include tablet {
    min-width: 152px;
  }

  @include desktop {
    min-width: 147px;
  }
}

.js-my-library-header__sticky {
  display: none;
}

.my-library-header__sticky {
  padding-top: 5px;
  padding-bottom: 5px;
  width: 100%;
  display: block;
  position: fixed;
  z-index: 100;
  background-image: linear-gradient(rgba(255, 0, 27, 0.7), rgba(0, 0, 0, 0.7));
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
  animation-name: show_header;
  animation-duration: 500ms;
  animation-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
}

@keyframes show_header {
  0% {
    opacity: 0;
    top: -70px;
  }
  100% {
    opacity: 1;
    top: 0;
  }
}

.my-library-header__container__sticky {
  display: flex;
  align-items: center;
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.my-library-header__links__item__sticky {
  margin-right: 15px;
  @media screen and(min-width:480px) {
    margin-right: 40px;
  }
  &:last-child {
    margin-right: 0;
  }
  &.--current {
    border-bottom: 3px solid #ff001b;
  }
}
